import "./../../configs/dotenv";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

/* import Kieee Rendering */
import { InitialDataContext } from "./../Kieee";

/* import components */
import Title from "./../Title";

/* import images*/

/* import utils */
import { maskCurrencyReal } from "./../../utils/functions";

/* import services */
import api from "./../../services/api";

/* import styles */
import { Container, Description, Items, Item, Header, HeaderTitle, Content } from "./styles";
import { Line, Price, SmalLine, Button } from "./styles";

function Plans() {
  //const initialData = InitialDataContext;

  //console.log("dentro: ", initialData);

  const [plans, setPlans] = useState([]);

  const getLines = lines => {
    const items = lines.split("\n");

    return items;
  };

  useEffect(() => {
    async function load() {
      const { data } = await api.get("/produtos/situacao/ativo");
      if (data) {
        setPlans(data);
      }
    }

    load();
  }, [InitialDataContext]);

  return (
    <Container id="planos">
      <Title title1="Conhe√ßa nossos" title2="Planos" styles={{ marginBottom: "20px" }} />

      <Description>
        <span className="black">
          N√£o perca est√° oportunidade e garanta j√°, mais seguran√ßa e sa√∫de para voc√™ e toda fam√≠lia. Pre√ßos <strong>acess√≠veis</strong>, descontos{" "}
          <strong>reais</strong> e muitas <strong>vantagens</strong>.
        </span>
      </Description>

      <Items>
        {plans.map((item, index) => (
          <Item key={index}>
            <Header>
              <HeaderTitle>
                <span>Vida</span>
                {item.DescricaoProduto}
              </HeaderTitle>
            </Header>
            <Content>
              {getLines(item.BeneficiosProduto).map(line => (
                <Line key={line}>{line}</Line>
              ))}

              {getLines(item.DiferencialProduto).map(line => (
                <Line key={line}>{line}</Line>
              ))}

              <Price>
                <span>R$</span>
                {item.ValorProduto ? maskCurrencyReal(item.ValorProduto) : "Sob Consulta"}
              </Price>
              {item.ValorAdesao ? <SmalLine>+ ades√£o de R$ {maskCurrencyReal(item.ValorAdesao)}</SmalLine> : <></>}

              <Link to={process.env.REACT_APP_PAGE_CONSTRUCTION === "true" ? `/site/criar-conta/${item._id}` : `/criar-conta/${item._id}`}>
                <Button>ASSINE AGORA</Button>
              </Link>
            </Content>
          </Item>
        ))}
      </Items>
    </Container>
  );
}

export default Plans;
